import appStore from "../store";

const getRandomTime = () => {
  const now = new Date();
  const utcMidnight = new Date(now);
  utcMidnight.setUTCHours(24, 0, 0, 0);
  const difference = utcMidnight.getTime() - now.getTime();
  const hoursLeft = Math.floor(difference / (1000 * 60 * 60));
  const randomSeconds = Math.floor(Math.random() * (120 - 15 + 1)) + 15;
  const randomMilliseconds = randomSeconds * 1000;
  const randomTime = now.getTime() + randomMilliseconds;
  return new Date(randomTime);
};

export default getRandomTime;
