  const [walletName, setWalletName] = useState("");

  useEffect(() => {
    setConnected(tonConnectUI.connected);
  }, [tonConnectUI.connected]);

  useEffect(() => {
    try {
      setWalletLogo(tonConnectUI.wallet.imageUrl);
      setWalletName(tonConnectUI.wallet.name);
    } catch {
      console.log("Failed to get info from wallet");
    }
  }, [tonConnectUI.connected]);

  const handleConnect = async () => {
    try {
      await tonConnectUI.connectWallet().then(() => {
        setConnected(true);
      });
    } catch (error) {
      console.error("Failed to connect wallet:", error);
    }
  };

  const handleDisconnect = async () => {
    try {
      setOpened(false);
      await tonConnectUI.disconnect().then(() => {
        setConnected(false);
      });
    } catch (error) {
      console.error("Failed to disconnect wallet:", error);
    }
  };

  const [opened, setOpened] = useState(false);

  const openWallet = () => {
    if (opened) {
      setOpened(false);
    } else {
      setOpened(true);
    }
  };

  const [copied, setCopied] = useState(false);
  const timerRef = useRef(null);

  const copy = () => {
    WebApp.HapticFeedback.notificationOccurred("success");
    navigator.clipboard.writeText(address);
    if (timerRef.current) {
      clearTimeout(timerRef.current);
      setCopied(false);
      setTimeout(() => {
        setCopied(true);
      }, 150);
    } else {
      setCopied(true);
    }

    timerRef.current = setTimeout(() => {
      setCopied(false);
      timerRef.current = null;
    }, 1500);
  };

