import React, { useState, useEffect } from "react";
import { Routes, Route } from "react-router-dom";
import { TonConnectUIProvider } from "@tonconnect/ui-react";
import { connect } from "../constants/constants";
import appStore from "../store";
import Home from "../pages/Home";
import Frens from "../pages/Frens";
import Boost from "../pages/Boost";
import Wallet from "../pages/Wallet";
import Memeverse from "../pages/Memeverse";
import FeaturesStories from "../pages/FeaturesStories";
import Claim from "../pages/Claim";
import Buy from "../pages/Buy";
import Stake from "../pages/Stake";
import Referral from "../pages/Referral";
import Party from "../pages/Party";
import BackgroundProcesses from "../components/BackgroundProcesses";
import Withdraw from "../pages/Withdraw";
import Assets from "../pages/Assets";

function AppRouter() {
  useEffect(() => {
    setTimeout(() => {
      appStore.showPage = true;
    }, 1000);
  }, []);
  return (
    <>
      <TonConnectUIProvider manifestUrl={connect}>
        <BackgroundProcesses />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/frens" element={<Frens />} />
          <Route path="/boost" element={<Boost />} />
          <Route path="/wallet" element={<Wallet />} />
          <Route path="/memeverse" element={<Memeverse />} />
          <Route path="/claim" element={<Claim />} />
          <Route path="/features-stories" element={<FeaturesStories />} />
          <Route path="/buy" element={<Buy />} />
          <Route path="/withdraw" element={<Withdraw />} />
          <Route path="/stake" element={<Stake />} />
          <Route path="/referral" element={<Referral />} />
          <Route path="/party" element={<Party />} />
          <Route path="/assets/:asset" element={<Assets />} />
        </Routes>
      </TonConnectUIProvider>
    </>
  );
}

export default AppRouter;
