import { observable, action, makeObservable } from "mobx";
import getLimit from "./utils/getLimit";
import getSpeed from "./utils/getSpeed";

class AppStore {
  userId = 0;
  balance = 0;
  counter = 0;
  progress = 1000;
  activeRocket = false;
  showRocket = false;
  placeRocket = 4;
  betRocket = 2;
  turbo = 3;
  fullEnergy = 3;
  multitap = 0;
  energyLimit = 0;
  rechargingSpeed = 0;
  autoTapBot = 0;
  showPage = false;

  constructor() {
    makeObservable(this, {
      userId: observable,
      balance: observable,
      counter: observable,
      progress: observable,
      activeRocket: observable,
      showRocket: observable,
      placeRocket: observable,
      betRocket: observable,
      turbo: observable,
      fullEnergy: observable,
      multitap: observable,
      energyLimit: observable,
      rechargingSpeed: observable,
      autoTapBot: observable,
      showPage: observable,
      updateBalance: action,
      updateCounter: action,
      updateProgress: action,
      updateActiveRocket: action,
      updateShowRocket: action,
      updatePlaceRocket: action,
      updateBetRocket: action,
      updateShowPage: action,
    });

    this.startProgressUpdate();
  }

  updateBalance(amount) {
    if (this.balance + amount >= 0) {
      this.balance += amount;
    } else {
      this.balance = 0;
    }
  }

  updateCounter(amount) {
    if (this.counter + amount >= 0) {
      this.counter += amount;
    } else {
      this.counter = 0;
    }
  }

  updateProgress(amount) {
    if (this.progress + amount >= 0 && this.progress + amount <= getLimit()) {
      this.progress += amount;
    } else if (this.progress + amount < 0) {
      this.progress = 0;
    }
  }

  updateActiveRocket(item) {
    this.activeRocket = item;
  }
  updateShowRocket(item) {
    this.showRocket = item;
  }
  updatePlaceRocket(item) {
    this.placeRocket = item;
  }
  updateBetRocket(item) {
    this.betRocket = item;
  }

  updateShowPage(item) {
    this.showPage = item;
  }

  startProgressUpdate() {
    setInterval(() => {
      this.updateProgress(getSpeed());
    }, 1000);
  }
}

const appStore = new AppStore();
export default appStore;
