import appStore from "../store";

const getSpeed = () => {
  if (appStore.rechargingSpeed === 0) {
    return 1;
  } else if (appStore.rechargingSpeed === 1) {
    return 2;
  } else if (appStore.rechargingSpeed === 2) {
    return 3;
  } else {
    return 4;
  } 
};

export default getSpeed;
