import React, { useState, useEffect, useRef } from "react";
import { observer } from "mobx-react";
import appStore from "../../store";
import getCurrentTime from "../../utils/getCurrentTime";
import getRandomTime from "../../utils/getRandomTime";
import getRandomPlace from "../../utils/getRandomPlace";

const BackgroundProcesses = observer(() => {
  const [rocketTime, setRocketTime] = useState(getRandomTime());

  useEffect(() => {
    appStore.placeRocket = getRandomPlace();
  }, []);

  const [currentTime, setCurrentTime] = useState(getCurrentTime());

  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentTime(getCurrentTime());
    }, 1000);

    return () => clearInterval(intervalId);
  }, []);

  useEffect(() => {
    // console.log(currentTime, rocketTime);
    const timeMatch =
      Math.abs(currentTime.getTime() - rocketTime.getTime()) < 1000;
    if (timeMatch) {
      getShowRocket();
    }
  }, [currentTime]);

  const getShowRocket = () => {
    appStore.showRocket = true;
    setTimeout(() => {
      appStore.showRocket = false;
      setRocketTime(getRandomTime());
      appStore.placeRocket = getRandomPlace();
    }, 5000);
  };
  return <></>;
});

export default BackgroundProcesses;
