import appStore from "../store";

const getPrice = (item) => {
  let level;
  if (item === "multitap") {
    level = appStore.multitap;
  } else if (item === "energy_limit") {
    level = appStore.energyLimit;
  } else if (item === "recharging_speed") {
    level = appStore.rechargingSpeed;
  } else {
    level = appStore.autoTapBot;
  }
  if (
    item === "multitap" ||
    item === "energy_limit" ||
    item === "recharging_speed"
  ) {
    if (level === 0) {
      return 100;
    } else if (level === 1) {
      return 2000;
    } else if (level === 2) {
      return 4000;
    } else if (level === 3) {
      return 8000;
    } else if (level === 4) {
      return 16000;
    } else if (level === 5) {
      return 32000;
    } else if (level === 6) {
      return 64000;
    } else if (level === 7) {
      return 128000;
    } else if (level === 8) {
      return 256000;
    } else if (level === 9) {
      return 512000;
    } else if (level === 10) {
      return 1024000;
    } else if (level === 11) {
      return 2048000;
    } else if (level === 12) {
      return 4096000;
    } else if (level === 13) {
      return 8192000;
    } else if (level === 14) {
      return 16384000;
    } else if (level === 15) {
      return 32768000;
    } else if (level === 16) {
      return 65536000;
    } else if (level === 17) {
      return 131072000;
    } else if (level === 18) {
      return 262144000;
    } else if (level === 19) {
      return 524288000;
    } else {
      return 1048576000;
    }
  } else {
    return 20000;
  }
};

export default getPrice;
